body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  display: grid;
  grid-template-columns: 1fr;
}

.footer {
  height: 10vh;
  color: white;
  background-color: black;
  padding-left: 25px;
}

.newsRow {
  padding-left: 15%;
  padding-right: 15%;
}

hr {
  border: 2px solid lightgrey;
  border-radius: 5px;
  margin-top: 15px;
}

.researchBtn {
  padding: 10px;
  margin-top: 2%;
  background-color: #2ecc71;
  border-style: none;
  font-size: 1.15em;
  cursor: pointer;
  color: white;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

.articleBtn {
  padding: 10px;
  margin-top: 2%;
  background-color: #3498db;
  border-style: none;
  font-size: 1.15em;
  cursor: pointer;
  color: white;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

.articleBtn:hover,
.researchBtn:hover {
  -webkit-transform: translateY(-3px);
          transform: translateY(-3px);
  box-shadow: 0 0.5rem 1.5rem rgba(0, 0, 0, 0.2);
}

.header {
  color: white;
  font-size: 1.25em;
  background-color: dimgray;
  padding-left: 10px;
}

.footer {
  margin-top: 10px;
  color: white;
  display: grid;
  -webkit-align-items: center;
          align-items: center;
  background-color: black;
  text-align: center;
}

.newsApi {
  text-decoration: none;
  color: #2ecc71;
}

.github {
  text-decoration: none;
  color: #3498db;
}

